@import '~node_modules/modern-normalize/modern-normalize.css';

/*

  font-family: "Noto Serif Display", serif;

  font-family: "Open Sans", sans-serif;

*/

html,
body {
  width: 100%;
  height: 100%;

  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 1.4;
  color: var(--light-color);

  scroll-behavior: smooth;
}

.no-scroll {
  overflow: hidden;

  padding-right: 15px;
}

.page__wrapper {
  display: flex;
  flex-direction: column;

  min-height: 100%;
}

.main {
  flex-grow: 1;
}
